@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@700&display=swap);
body {
  font-family: "Roboto", sans-serif;
}

h1,
p,
.typed-text-animation {
  font-family: "Orbitron", sans-serif;
}

nav a.nav-link {
  font-weight: 400;
  letter-spacing: 0.04rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.nav-link {
  color: #fff !important;
}

.bg-transparent {
  background-color: rgba(0, 0, 0, 0.61) !important;
}
.logo-png {
  width: 10rem;
}
.navbar {
  position: fixed !important;
  width: 100vw;
}

:root {
  --primary-dark: #0f0f0fe7;
  --primary-orange: #e66f00;
  --primary-white: #ffffff;
  --primary-gradient: linear-gradient(360deg, #000000, #000000f6);
}

.header-wrapper {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(360deg, #000000, #000000f6);
  background: var(--primary-gradient);
  color: #e66f00;
  color: var(--primary-orange);
}

.main-info {
  height: 100vh;
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  text-transform: uppercase;
  padding-left: 10rem;
}

.main-info h1 {
  font-weight: 900;
  font-size: 10rem;
  z-index: 2;
}
.main-info p {
  font-size: 5rem;
}

.typed-text-animation {
  color: #ffffff;
  color: var(--primary-white);
  font-size: 3rem;
}

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

